
body, html {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f4; 
  color: #333; 
}

header {
  background-color: #2c3e50; 
  padding: 1rem 2rem;
  transition: background-color 0.3s ease; 
}

header:hover {
  background: linear-gradient(135deg, #3498db, #e74c3c); 
}

header nav {
  display: flex;
  justify-content: flex-end; 
  align-items: center;
}

header ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

header ul li {
  margin: 0 1.5rem;
}

header ul li a {
  color: #ecf0f1;
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: 500;
  padding: 0.75rem 1rem;
  border-radius: 4px;
  transition: all 0.3s ease;
}

header ul li a:hover {
  background-color: rgba(255, 255, 255, 0.2); 
  color: white;
}


.hero {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  background-color: #34495e;
}

.hero h1 {
  font-size: 3rem;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}

.hero p {
  font-size: 1.5rem;
}

.cta-button {
  padding: 0.8rem 2rem;
  background-color: #073c55d3; 
  color: white;
  border: none;
  border-radius: 5px;
  text-decoration: none;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.cta-button:hover {
  background-color: #082642; 
}


.features {
  padding: 3rem;
  background-color: #f9f9f9;
  text-align: center;
}
.features h2 {
  margin-bottom: 2rem;
  color: #3498db; 
  font-size: 3rem;
  font-weight: 700; 
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  letter-spacing: 1px; 
  padding: 0.5rem 1rem;
  background-color: rgba(52, 152, 219, 0.1); 
  border-radius: 5px; 
  display: inline-block; 
}

.features-grid {
  display: flex;
  justify-content: space-around;
}

.feature-item {
  max-width: 300px;
  background-color: white;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease;
}

.feature-item:hover {
  background-color: #ecf0f1;
}

.feature-item h3 {
  color: #2c3e50; 
}


.about {
  padding: 3rem;
  background-color: #f4f4f4; 
  text-align: center;
}

.about h2 {
  margin-bottom: 1.5rem;
  color: #3498db; 
  font-size: 3rem; 
  font-weight: 700;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3); 
  letter-spacing: 1px; 
  padding: 0.5rem 1rem; 
  background-color: rgba(52, 152, 219, 0.1); 
  border-radius: 5px;
  display: inline-block; 
}

.about p {
  max-width: 800px;
  margin: 0 auto;
  font-size: 1.2rem;
  line-height: 1.6;
  color: #555; 
}


.contact {
  padding: 3rem;
  background-color: #2c3e50; 
  color: white;
  text-align: center;
}

.contact input {
  padding: 0.5rem;
  margin-right: 0.5rem;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.contact button {
  padding: 0.5rem 1rem;
  background-color: #e74c3c;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.contact button:hover {
  background-color: #c0392b; 
}


footer {
  background-color: #1a1a1a; 
  color: #ecf0f1;
  text-align: center;
  padding: 1.5rem 0;
  transition: background-color 0.3s ease; 
}

.feature-item:hover {
  transform: translateY(-10px);
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
  background: linear-gradient(135deg, #3498db, #e74c3c);
  color: white; 
}


footer {
  background-color: #5d6b79; 
  color: #ecf0f1;
  text-align: center;
  padding: 1.5rem 0;
  transition: background-color 0.3s ease; 
}

footer:hover {
  background: linear-gradient(135deg, #3498db, #e74c3c); 
}

footer p {
  margin: 0;
  font-size: 0.9rem;
}


footer a {
  color: #ecf0f1;
  text-decoration: none;
  transition: color 0.3s ease; 
}

footer a:hover {
  color: #ffffff; 
  text-decoration: underline; 
}